body{color:white;font-family:"IBM Plex Sans", sans-serif;margin:0px 8px 8px 8px;background-color:#fcfcf8}.body-wrapper{margin:auto;padding:0;width:60vw;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.body-head{height:15%;width:100vw;font-size:4em;padding:0.1em 1em 0.1em 1em;text-align:center;color:black;background-color:#a9e6c2}.body-content{width:100%;background-color:#fcfcf8;color:#1F2421;padding:1em;font-size:2em}#filter{padding:1em;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-align:none;-ms-flex-align:none;align-items:none;background:#fcfcf8;top:-10px;border-bottom:3px solid black}.sticky{position:fixed;top:0px;right:0px;width:95vw;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);margin-bottom:10%}.content-results{padding:1em;border-bottom:3px solid #1F2421;font-size:0.7em}.content-results h2{-webkit-margin-before:0.03em;margin-block-start:0.03em;-webkit-margin-after:0.10em;margin-block-end:0.10em}.content-results:nth-child(2){background:white}#res-header{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-align:center;-ms-flex-align:center;align-items:center}#htl-img{width:100%}#res-title{display:inline-block}#star{display:inline-block}#rev{padding-bottom:0.5}.reviews{margin:0.5em 0 0.5em 0;padding:1.5em;background:#bcddbc}.reviews p{font-size:14px}.hideRev{display:none}button{margin-left:1em;padding:0.5em;border-width:2px;border-style:solid;-o-border-image:initial;border-image:initial;border-color:black;border-radius:100px;text-decoration:none;color:#1F2421;background:#fcfcf8;cursor:pointer}button:hover{color:#a9e6c2}#search-btn{background:#a9e6c2}#search-btn:hover{color:white}#select-filter{padding:0.5em}select{-webkit-box-shadow:none;box-shadow:none;border:1px solid rgba(34,36,38,0.15);border-radius:.9rem;background-color:#fcfcf8}select:after{border-color:transparent transparent #fff transparent}option:hover{background:#8cc0a2}#flight-modal{display:none;width:50%;top:30%;background:#a9e6c2;border:3px solid black;position:fixed;font-size:1.5em;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;padding:1.5em;color:black}#flex-wrap{width:70%;padding:1em}@media only screen and (max-width: 600px){.body-head{font-size:2em}.body-wrapper{width:80vw}.sticky{width:100vw}}@media only screen and (min-width: 600px){.body-head{font-size:2em}.body-wrapper{width:80vw}.sticky{width:100vw}}@media only screen and (min-width: 768px){.body-head{font-size:2em}.body-wrapper{width:80vw}.sticky{width:100vw}}
